import React, { useEffect, useState } from "react";
import ReactMarkdown from "react-markdown";
import remarkGfm from "remark-gfm";
import {
  loadWritingIndex,
  loadMarkdown,
} from "../lib/content";
import "./Writing.css";

export default function Writing() {
  const [posts, setPosts] = useState([]);
  const [current, setCurrent] = useState(null);
  const [body, setBody] = useState("");
  const [loading, setLoading] = useState(true);
  const [loadingBody, setLoadingBody] = useState(false);

  // 
  useEffect(() => {
    async function init() {
      try {
        setLoading(true);
        const index = await loadWritingIndex();

        index.sort((a, b) => new Date(b.date) - new Date(a.date));

        setPosts(index);

        if (index.length) {
          setCurrent(index[0]);
        }
      } catch (e) {
        console.error(e);
      } finally {
        setLoading(false);
      }
    }

    init();
  }, []);

  // 
  useEffect(() => {
    if (!current) {
      setBody("");
      return;
    }

    async function loadBody() {
      try {
        setLoadingBody(true);
        const { body } = await loadMarkdown(current.file);
        setBody(body);
      } catch (e) {
        console.error(e);
        setBody("");
      } finally {
        setLoadingBody(false);
      }
    }

    loadBody();
  }, [current]);

  if (loading) {
    return <div style={{ padding: 24 }}>...</div>;
  }

  if (!posts.length) {
    return <div style={{ padding: 24 }}></div>;
  }

  return (
    <div className="doc-layout">
      {/*  */}
      <aside className="doc-sidebar">
        <div className="doc-sidebar-title"></div>
        <ul className="doc-list">
          {posts.map((p) => (
            <li key={p.slug}>
              <button
                type="button"
                className={
                  "doc-list-item" +
                  (p.slug === current?.slug ? " doc-list-item-active" : "")
                }
                onClick={() => setCurrent(p)}
              >
                <div className="doc-list-title">{p.title}</div>
                {p.summary && (
                  <div className="doc-list-desc">{p.summary}</div>
                )}
              </button>
            </li>
          ))}
        </ul>
      </aside>

      {/*  */}
      <article className="doc-main">
        <div className="doc-tag">WRITING</div>
        <h1 className="doc-title">{current.title}</h1>
        <div className="doc-meta">
          {current.date}
          {current.lang ? `  ${current.lang}` : null}
        </div>

        {loadingBody ? (
          <div style={{ marginTop: 12 }}>...</div>
        ) : (
          <div className="markdown-body">
            <ReactMarkdown remarkPlugins={[remarkGfm]}>
              {body}
            </ReactMarkdown>
          </div>
        )}
      </article>

      {/*  */}
      <aside className="doc-right">
        <div className="doc-right-card">
          <div className="doc-right-title"></div>
          <div className="doc-right-content">
            <p>{current.detail || current.summary ||""}</p>
          </div>
        </div>
      </aside>
    </div>
  );
}
