import React from "react";
import ThemeToggle from "../theme/ThemeToggle";
import "./Header.css";

const NAV_ITEMS = [
  { id: "home", label: "" },
  { id: "writing", label: "" },
  { id: "photography", label: "" },
  { id: "about", label: "" },
];

export default function Header({ currentPage, onChangePage, onBackLanding }) {
  return (
    <header className="site-header">
      <div className="site-header-inner">
        {/*  */}
        <div
          className="site-logo site-logo-clickable"
          onClick={onBackLanding}
          role="button"
          tabIndex={0}
          onKeyDown={(e) => {
            if (e.key === "Enter" || e.key === " ") onBackLanding?.();
          }}
          aria-label=""
        >
          <span className="site-logo-text">Haotlas</span>
        </div>

        <nav className="site-nav" aria-label="">
          {NAV_ITEMS.map((item) => (
            <button
              key={item.id}
              type="button"
              className={
                "site-nav-item" +
                (currentPage === item.id ? " site-nav-item-active" : "")
              }
              onClick={() => onChangePage(item.id)}
            >
              {item.label}
            </button>
          ))}
        </nav>

        {/*  */}
        <div className="site-actions">
          <ThemeToggle />
        </div>
      </div>
    </header>
  );
}
